import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { UnitUsersReportComponent } from './unit-users-report.component';
import { UnitUsersReportDataComponent } from './unit-users-report-data/unit-users-report-data.component';

export const routes: Routes = [
  { path: '', component: UnitUsersReportComponent, canActivate: [AuthGuard] },
	{ path: 'reports/:name/:id', component: UnitUsersReportDataComponent, canActivate: [AuthGuard] },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UnitUsersReportRoutingModule { }



